.graph-menu-display {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px; /* Espacio entre los botones */
}

.graph-date {
    margin: 0 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;
    color: #333;
    min-width: 150px; /* Asegura que el span tenga un ancho consistente */
    text-align: center; /* Centra el texto dentro del span */
}

.graph-menu-button {
    display: flex;
    flex-direction: row; /* Alinea el ícono y el texto en columna */
    align-items: center; /* Centra el contenido (ícono y texto) */
    gap: 5px; /* Espacio entre el ícono y el texto */
    padding: 12px 20px;
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    border: none;
}

.graph-menu-button:hover {
    background-color: #45a049;
}

.graph-container {
    padding: 20px;
    margin-top: 40px;
    max-width: 900px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .graph-container {
        padding: 0px;
        margin-top: 40px;
        max-width: 900px;
        margin: 0 auto;
    }
    
}